.ContactMethod {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  scale: 0.2;
  background-size: cover;
  background-repeat: no-repeat;
}
.ContactMethod:hover {
  opacity: 0.4;
}

@media (max-width: 500px) {
  .ContactMethod {
    width: 30px;
    height: 30px;
    margin: 0 10px;
  }
}